@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-s;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #f5f5f5;
  -wekit-font-smoothing: antialiased !important;
}

textarea,
input,
button {
  font: 400 18px Roboto, sans-serif;
}

button {
  cursor: pointer;
}

form input {
  width: 100%;
  height: 60px;
  color: #333;
  border: 1px solid #dcdee6;
  border-radius: 8px;
  padding: 0 20px;
}
form textarea {
  width: 100%;
  resize: vertical;
  min-height: 140px;
  color: #333;
  border: 1px solid #dcdee6;
  border-radius: 8px;
  padding: 16px 20px;
  line-height: 24px;
}
.button {
  width: 100%;
  height: 100%;
  background: #6c63ff;
  border: 0;
  border-radius: 8px;
  color: #fff;
  font-weight: bold;
  margin-top: 16px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  line-height: 60px;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
}

.button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

.back-link {
  display: flex;
  align-items: center;
  margin-top: 40px;
  color: #41414d;
  font-size: 18px;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.2s;
}

.back-link svg {
  margin-right: 8px;
}

.back-link:hover {
  opacity: 0.8;
}

.linkSubMenu{
  text-Decoration: none;
  display: block;
  color: #000000;
  padding: 10px;
  font-family: Roboto;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.75px;
  text-transform: uppercase;
}

.linkSubMenu:hover {
  text-Decoration: none;
  display: block;
  color: #00692C;
  padding: 10px;
  font-weight: bold;
}

.page-container {
  position: relative;
  background-color: white;
}

.page {
  position: relative;
  page-break-after: auto;
  -webkit-column-break-after: auto;
          break-after: auto;
  page-break-after: always;
}

.page-content {
  position: relative;
}

.header-content {
  position: relative;
  padding: 2px;
  line-height: 10px;
}

.model-fab {
  display: flex;
  justify-content: space-between;
}

.border-black {
  border: 1px solid black;
  padding: 5px;
}

.text-center {
  text-align: center;
  margin: 5px;
}

.title-board-blank {
  margin-top: 10px;
  padding: 10px;
  font-weight: bold;
  border: solid 1px black;
}

.board-blank {
  min-height: 6cm;
  border: solid 1px black;
}

.pagina-tabela {
  border: none;
}
.mt-10 {
  margin-top: 1rem;
}

body {
  background-color: #fff;
}
.portaint-print @page {
  size: A4 portrait;
  margin: 1.5cm;
}


.landscape-print @page {
  size: A4 landscape;
  margin: 1.5cm;
}

@media print {
  .pagina-tabela {
    height: 100%;
    width: 100%;
    border: none;
  }
}
